/* ------------------------------- */

.command-list-container {
  width: 13%;
  height: calc(100vh - (100px + 100px));
  height: calc(100vh - (var(--topMenuHeight) + var(--bottomMenuHeight)));
  /* height: 85vh; */
  /* position: fixed; */
  padding: 0rem 0rem 0rem 1rem;
}

.command-list {
  height: 100%;
  width: 100%;
  border-radius: 0.4rem;
  background-color: #fafafb;
  overflow: auto;
}

.command-list-wrapper {
  height: -webkit-max-content;
  height: max-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

/* .command-list-width-box {
  width: 13%;
  height: 85vh;
} */

.command-selector {
  min-width: 80%;
  display: flex;
  align-items: center;
  border-radius: 3px;
  margin: 1rem 1rem 1rem 0.7rem;
  padding: 0.5rem 0.3rem 0.5rem 0.5rem;
  background-image: linear-gradient(#63656a, #585b60, #63656a);
  color: #fef7f5;
  cursor: grab;
  font-weight: 600;
  white-space: nowrap;
}

.command-selector:hover {
  transition: ease-in-out 0.15s;
  opacity: 0.4;
}

.command-selector i {
  color: white;
  margin-right: 0.5rem;
  font-size: 0.5rem;
}

#kurikaeshi-selector {
  background-color: #E7E7EB !important;
  background-image: none;
  color: #585b60;
  border: solid #c2c2c2 5px;
}

#kurikaeshi-selector:hover {
  transition: ease-in-out 0.15s;
  opacity: 0.4;
}

#kurikaeshi-selector i {
  color: #585b60;
  margin-right: 0.5rem;
  font-size: 0.5rem;
}

/* ------------------------------- */
.center-section {
  display: flex;
  width: 100%;
  /* min-height: 85vh; */
  min-height: calc(100vh - (100px + 100px));
  min-height: calc(100vh - (var(--topMenuHeight) + var(--bottomMenuHeight)));
}

.main-interface-section {
  display: flex;
  width: 87%;
  /* min-height: 85vh; */
  min-height: calc(100vh - (100px + 100px));
  min-height: calc(100vh - (var(--topMenuHeight) + var(--bottomMenuHeight)));
}

/* ------------------------ */
.top-menu-container {
  /* min-height: 15vh; */
  height: 100px;
  height: var(--topMenuHeight);
  padding: 1rem 1rem;
  position: fixed;
  background-color: #4b4b53;
  width: 100%;
  z-index: 1500;
}

.top-menu-spacer {
  /* height: 15vh; */
  height: 100px;
  height: var(--topMenuHeight);
  width: 100%;
}

.top-menu {
  height: calc(100px - 2rem);
  height: calc(var(--topMenuHeight) - 2rem);
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: white;
  border-radius: 0.4rem;
}

.top-menu-button {
  color: rgb(38, 43, 71);
  border: solid 2px rgba(38, 43, 71, 0);
  font-weight: bold;
  border-radius: 0.2rem;
  text-align: center;
  padding: 0.2rem 0.8rem;
  font-size: 0.8rem;
  cursor: pointer;
  margin: 0 0 0 1rem;
  position: relative;
}

.top-menu-button i {
  margin-right: 0.2rem;
}

.top-menu-button:hover {
  border: solid 2px rgb(38, 43, 71);
  transition: ease 0.2s;
}

#top-menu-file-upload,
#top-menu-file-save {
  display: none;
}

