/* ----------------------- */
.tannikannsann-popup {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #fafafb;
  width: 40%;
  height: 50%;
  border-radius: 0.7rem;
  z-index: 150000;
  padding: 1rem;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.close-popup {
  position: absolute;
  bottom: 100%;
  left: 100%;
  color: rgba(255, 255, 255, 0.8);
  /* text-shadow: 3px 4px 2px rgba(255, 105, 135, 0.3); */
  font-size: 1.6rem;
  cursor: pointer;
}

.close-popup:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  transition: ease 0.1s;
}

/* -- */
.wizard-controller {
  height: 100%;
  width: 100%;
}

.tanni-wizard {
  height: 100%;
  width: 100%;
  position: relative;
}

.tanni-wizard input[type="text"] {
  text-align: center;
}

.tanni-wizard-title {
  position: absolute;
  top: 5%;
  left: 10%;
  font-weight: bold;
}

.tanni-wizard-selector {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  padding: 0.2rem;
  display: flex;
  flex-direction: column;
}

.tanni-wizard-buttons {
  position: absolute;
  bottom: 5%;
  right: 10%;
  z-index: 1;
}

.tanni-back {
  margin-right: 0.3rem !important;
}

/* -- */
.tanni-jiku {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row !important;
  flex-wrap: wrap;
}

.jiku-input-container {
  padding: 0.5rem;
}

.tanni-kikou {
  margin-left: 30%;
}
/* -- */
.tanni-susumi,
.tanni-bunkai {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.susumi-1,
.bunkai-1 {
  display: flex;
  align-items: center;
}
.susumi-1 p,
.bunkai-1 p {
  margin: 0 0.3rem;
}
.susumi-2,
.bunkai-2 {
  margin: 3rem 0 0 0;
  font-size: 0.9rem;
  color: rgba(0, 0, 0, 0.8);
}
.susumi-2 span,
.bunkai-2 span {
  font-weight: bold;
  font-size: 1rem;
}
/* -- */
.gensoku-selector {
  opacity: 0.4;
}

.gensoku-selector td {
  text-align: center;
}

/* -------------------------- */
.dropdown-selector {
  display: flex;
  align-items: center;
}

.dropdown-selector {
  position: relative;
}

.selected-item {
  font-size: 0.9rem;
  border: 1.5px solid rgba(0, 0, 0, 0.1);
  padding: 0.1rem;
  min-width: 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.selected-item-smaller {
  font-size: 0.7rem;
  border: 1.5px solid rgba(0, 0, 0, 0.1);
  padding: 0.1rem;
  min-width: 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  z-index: 2000;
}

.dropdown-items {
  display: none;
  position: absolute;
  top: 100%;
  box-shadow: 0 2px 2px 1px rgba(255, 105, 135, 0.2);
  z-index: 2100;
  width: 100%;
  background-color: white;
}

.dropdown-item {
  font-size: 0.8rem;
  padding: 0.1rem;
  background-color: white;
  border: 1px solid rgba(0, 0, 0, 0.1);
  cursor: pointer;
  z-index: 2200;
}

.dropdown-item:hover {
  color: rgba(0, 0, 0, 0.5);
  transition: ease 0.2s;
}

.dropdown-selector i {
  color: rgba(0, 0, 0, 0.4);
  font-size: 0.9rem;
  cursor: pointer;
  margin: 0 0.1rem;
}

.dropdown-selector i:hover {
  color: rgba(0, 0, 0, 0.8);
  transition: ease 0.2s;
}

/* ------------------------ */
.program-block-container {
  width: 75%;
  padding: 0 1rem 1rem 1rem;
  height: 100%;
}

.program-block {
  min-height: calc(85vh - 1rem);
  padding: 0rem 1rem 1rem 1rem;
  border-radius: 0.4rem;
  background-color: white;
}

.enter-button {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  margin: 0 0 2rem 0;
}

/* ------------------------------- */
.cml-output-container {
  width: 25%;
  height: 100%;
  padding: 0rem 1rem 1rem 0rem;
}

.cml-output-section {
  min-height: calc(85vh - 1rem);
  padding: 0.5rem 1rem;
  display: flex;
  justify-content: start;
  align-items: center;
  flex-direction: column;
  position: relative;
  background-color: white;
  border-radius: 0.4rem;
}

.cml-output-content {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.cml-output-content h3 {
  margin: 0.5rem auto;
  width: -webkit-max-content;
  width: max-content;
  font-size: 1.4rem;
  font-weight: bold;
}

.cml-editor-container {
  width: 100%;
}

/* .cml-editor {
  height: 1000px;
} */

.jikkou-button {
  margin: 1rem auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* width: fit-content; */
  width: 100%;
  font-size: 0.8rem;
  font-weight: bold;
}

#export-textfile {
  display: none;
}

/* .copy-cml-container {
  position: absolute;
  top: 60%;
  right: 10%;
  z-index: 90;
} */

.copy-cml {
  background-color: blue;
  height: 3rem;
  width: 3rem;
  border-radius: 50%;
  box-shadow: 0 3px 4px 2px rgba(255, 105, 135, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: rgba(255, 255, 255, 0.95);
  font-size: 1.1rem;
  cursor: pointer;
}

.copy-cml:hover {
  transition: ease-in-out 0.2s;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.exp-copy,
.exp-box,
.exp-copy-done {
  z-index: 100;
  display: none;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  min-height: -webkit-fit-content;
  min-height: -moz-fit-content;
  min-height: fit-content;
  position: absolute;
  bottom: 95%;
  left: 100%;
  /* border: solid 1px rgba(0, 0, 0, 0.1); */
  /* -- */
  /* border-top: solid 1px rgba(0, 0, 0, 0.1); */
  /* border-left: solid 1px rgba(0, 0, 0, 0.1); */
  font-size: 0.7rem;
  font-weight: normal;
  text-align: center;
  padding: 0.2rem 0.3rem;
  /* color: rgba(0, 0, 0, 0.7); */
  /* background-color: rgba(255, 255, 255, 0.8); */
  color: white;
  background-color: #585b60;
  border-radius: 0.2rem;
}

.exp-box {
  top: 115% !important;
  left: 40% !important;
}

.exp-copy-done {
  color: white;
  top: 50% !important;
  bottom: auto !important;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
}

/* ===== */
.popup-instruction {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 500;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.2);
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.popup-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}

.popup-content {
  background-color: white;
  border-radius: 1rem;
  height: 30vh;
  min-width: 30vw;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px;
}

.popup-instruction img {
  height: 50vh;
}

.popup-neveragain {
  display: flex;
  align-items: center;
  background: white;
  padding: 0.2rem;
  font-size: 0.9rem;
}

#close-instruction-popup {
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.6rem;
  cursor: pointer;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
  transition: ease 0.2s;
}

#close-instruction-popup:hover {
  opacity: 0.5;
}


/* ========================== */

.bottom-menu-container {
  /* min-height: 15vh; */
  height: 100px;
  height: var(--bottomMenuHeight);
  padding: 1rem 1rem;
  position: fixed;
  bottom: 0;
  background-color: #4b4b53;
  width: 100%;
  z-index: 150;
}

.bottom-menu-spacer {
  /* height: 15vh; */
  height: 100px;
  height: var(--bottomMenuHeight);
  width: 100%;
}

.bottom-menu {
  height: calc(100px - 2rem);
  height: calc(var(--bottomMenuHeight) - 2rem);
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #1976D2;
  /* background-color: orange; */
  border-radius: 0.4rem;
  cursor: pointer;
}

.bottom-menu:hover {
  opacity: 0.7;
}

#bottom-menu-button-text {
  font-weight: 500;
  color: white;
  font-size: 20px;
}
.program-grid {
  display: grid;
  grid-template-columns: 1fr 5fr;
  grid-gap: 0.3rem;
  gap: 0.3rem;
  padding: 2rem 0.2rem 0.5rem 0.2rem;
  overflow: visible;
}

.program-grid > div {
  text-align: center;
}
/* ---------------- */
.pg-left {
  grid-column: 1/2;
  /* gap: 0.6rem 0; */
}

.pglemp0 {
  height: 1.5rem;
}

.pglempgroup {
  height: 1.5rem;
  margin: 0.6rem;
}

.pg-l-empty-loop {
  height: 2.3rem;
  margin: calc(0.2rem + 0.4rem) 0;
  border-radius: 0.2rem;
  background-color: #e7e7eb;
  border: solid #c2c2c2 5px;
}

.pg-l-loop {
  /* height: 2.3rem; */
  margin: calc(0.2rem + 0.4rem) 0;
  border-radius: 0.2rem;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: rgb(235, 235, 235);
  position: relative;
  font-size: 0.9rem;
}

.loop-drag-top,
.loop-drag-bottom {
  position: absolute;
  width: 100%;
  border-radius: 0.2rem;
  overflow: hidden;
}

.loop-drag-top {
  top: 0;
}

.loop-drag-bottom {
  bottom: 0;
}

.loop-drag-top > div,
.loop-drag-bottom > div {
  width: 100%;
  background-color: rgb(209, 209, 209);
  height: 0.7rem;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: 0.6rem;
  color: rgba(0, 0, 0, 0.2);
}

.loop-drag-top > div:last-child,
.loop-drag-bottom > div:last-child {
  border-top: solid rgba(0, 0, 0, 0.1) 1px;
}

.loop-drag-top > div:hover,
.loop-drag-bottom > div:hover {
  background-color: rgba(0, 0, 0, 0.4);
  transition: ease 0.2s;
}

/* --- */
.loop-title {
  cursor: pointer;
}

.loop-title:hover {
  color: rgba(0, 0, 0, 0.4);
  transition: ease-in-out 0.2s;
}

.trash-block {
  margin: 0 0.2rem 0 0.6rem;
  font-size: 0.85rem;
  color: rgba(0, 0, 0, 0.4);
  cursor: pointer;
}

.trash-block i:hover {
  color: rgb(221, 63, 63);
  transition: ease-in-out 0.1s;
}

.pg-l-empty-loop:hover,
.pg-l-empty-loop-hovered {
  border: 2px solid rgb(74, 129, 192);
}

/* ---------------- */
.program-main-grid {
  display: grid;
  /* grid-template-columns: repeat(3, 1fr); */
  grid-gap: 0.2rem 0.5rem;
  gap: 0.2rem 0.5rem;
  overflow-x: auto;
}

.empty-right-box {
  width: 1rem;
}

.trash-row-right-box {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: rgb(185, 185, 185);
  cursor: pointer;
}

.trash-row-right-box i:hover,
.trash-jiku:hover {
  color: rgb(221, 63, 63);
  transition: ease-in-out 0.1s;
}

.move-row-cont {
  display: flex;
  flex-direction: column;
  font-size: 0.8rem;
  margin: 0 0 0 0.5rem;
  color: rgba(0, 0, 0, 0.2);
}

.move-row-up {
  margin-bottom: 0.3rem;
}

.move-row-up:hover,
.move-row-down:hover {
  color: rgba(0, 0, 0, 0.4) !important;
  transition: ease 0.2s;
}

.jiku-number {
  font-size: 0.9rem;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.8);
  height: 1.5rem;
  position: relative;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.move-jiku {
  cursor: pointer;
  color: rgba(0, 0, 0, 0.2);
}

.move-jiku:hover {
  color: rgba(0, 0, 0, 0.4);
  transition: ease 0.2s;
}

.trash-jiku {
  margin-left: 0.5rem;
  color: rgb(185, 185, 185);
  cursor: pointer;
  font-size: 0.8rem;
}

.add-jiku-left,
.add-jiku-right {
  height: 100%;
  background-color: white;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  width: 0.2rem;
  position: absolute;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}

.add-jiku-left {
  left: 0%;
}

.add-jiku-right {
  right: 0%;
}

.grid-line {
  /* grid-column: 1/4; */
  height: 1.5px;
  background-color: rgba(0, 0, 0, 0.4);
}

.pg-add-row {
  grid-column: 1/4;
  height: 0.2rem;
  background-color: rgb(168, 168, 168);
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  cursor: pointer;
  transition: 0.2s;
}

.add-jiku-plus-cont,
.add-row-plus-cont {
  display: none;
  background-color: rgb(168, 168, 168);
  height: 0.8rem;
  width: 0.8rem;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 0.5rem;
  z-index: 100;
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  transition: 0.2s;
}

.dousa-group-kaishi {
  /* grid-column: 1/4; */
  padding: 0.1rem 0;
  /* background-color: rgba(0, 0, 0, 0.1); */
  /* background-color: rgba(0, 0, 0, 0.3); */
  background-color: #22B573;
  /* background-color: #1871CD; */
  color: white;
  font-size: 0.7rem;
  font-weight: 600;
  border-radius: 0.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 1.5rem;

  /* border: solid  #63656a 2px; */
}

.dousa-box,
.dragged-box {
  border-radius: 0.2rem;
  font-weight: bold;
  /* color: rgba(0, 0, 0, 0.8); */
  color: #fef7f5;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 2.3rem;
  /* background-color: rgb(216, 221, 231); */
  /* background-color: rgb(192, 204, 226); */
  background-image: linear-gradient(#63656a, #585b60, #63656a);
  min-width: 7rem;
  cursor: pointer;
}

.dragged-box {
  opacity: 0.4;
}

.dousa-box p {
  cursor: pointer;
}

.no-value-circle {
  position: absolute;
  top: 2px;
  right: 2px;
  height: 0.4rem;
  width: 0.4rem;
  background-color: rgb(219, 219, 219);
  border-radius: 50%;
}

.typeDataInDousa {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: white;
